@import '../../assets/css/variable.less';
.mining-claim {
  height: 152px;
  border-bottom: 1px solid @divider_color ;
  .deri-num{
    color: @whiteFontColor;
    font-weight: bold;
  }
  .deri-num-slider {
    font-weight: normal;
    .deri-claim-time {
      font-size: 20px;
      line-height: 26px;
      color: #FFFFFF;
      margin-bottom: 8px;
    }
    .deri-claim-text {
      font-size: 14px;
      margin-bottom: 8px;
    }
    .deri-claim-slider{
      background-color: rgba(255, 255, 255, 0.1);
      border-radius: 2px;
    }
  }
}

@media only screen and (max-width: 374px) {
  .mining-claim {
    margin: 0 auto;
    padding: 0 24px;
    padding-top: 28px;
    height: 248px;
    .cliam-button{
      margin-top: 5px;
    }
    .deri-claim-text {
      margin-top: 5px;
    }
  }
}
@media only screen and (min-width : 375px) and (max-width:759px) {
  .mining-claim {
    margin: 0 auto;
    padding: 0 24px;
    padding-top: 28px;
    height: 248px;
    .cliam-button{
      margin-top: 5px;
    }
    .deri-claim-text {
      margin-top: 5px;
    }
  }
}
@media only screen and (min-width : 760px) and (max-width:1139px) {
  .mining-claim {
    .mining-claim-info {
      display: flex;
      margin: 0 auto;
      height: 152px;
      align-items: center;
      justify-content: space-between;
      padding: 0 24px;
  
      .deri-num-slider {
        width: 100%;
      }
    }
  }
}
@media only screen and (min-width : 1140px) and (max-width: 1567px) {
  .mining-claim {
    .mining-claim-info {
      display: flex;
      align-items: center;
      width: 1140px;
      height: 152px;
      justify-content: space-between;
      padding-left: 24px;
      padding-right: 24px;
      margin: 0 auto;
    }
  }
}

@media only screen and (min-width : 1568px) {
  .mining-claim {
    height: 152px;
    .mining-claim-info {
      display: flex;
      align-items: center;
      height: 152px;
      justify-content: space-between;
      padding-left: 48px;
      padding-right: 48px;
    }
  }
}
